import React from 'react';
import './TransactionItem.css'




const transactionItem = (props) => {
	const imageSrc = "https://s3.amazonaws.com/drackerprofiles/".concat(props.uid);
	return (
		<div className="TransactionItem">
			<div className = "TransactionItem-image"> 
				<img src = {imageSrc}  width="75" height = "75" alt={props.description}/>
			</div>
			<div className="TransactionItem-Details">
				<p>You owe John Doe {props.amount} for &quot;{props.description}&quot;</p>
			</div>
		</div>
	)
};

export default transactionItem;



