import React from 'react';
import TransactionItem from '../TransactionItem/TransactionItem'
import './Home.css'



const Home = (props) => {
    const transactions = (
      <div>
        {
          props.unsettled.map(transaction => {
            return <TransactionItem amount={transaction.amount} description={transaction.description} key={transaction.transactionId} uid={transaction.uid}/>
          })
        }
      </div>
    )
	return (
    	<div className="TransactionView" key="TransactionView">
    		{transactions}
    	</div>
	);
};

export default Home;




