import React, { Component } from 'react';
import {BrowserRouter, Route, Switch} from 'react-router-dom'
import Navigation from './Components/Navigation/Navigation'
import Home from './Components/Home/Home'
import $ from 'jquery'
import Error from './Components/Error/Error'

import './App.css'


class App extends Component {
  state = {
    unsettled: [],
    settled: [],
    loadingState: true
  }

  componentDidMount() {
    this.fetchData();
  }

  fetchData = () => {
    $.ajax({
      url: 'https://uwjxhdmmub.execute-api.us-east-1.amazonaws.com/prod/users/5HRFTXanyqYacSZA2AbkYWVpQFF2',
      headers: {
        'x-api-key': 'hzeUZDg5lB55PQaklNo3Y8l3hdBcq7rnGNVcrkR2'
      },
      success: (res) => {
        this.setState({unsettled: JSON.parse(res.unsettled), settled: JSON.parse(res.settled), loadingState: false})
      },
      error: (xhr, status, err) => {
        console.error("Failed to fetch data")
      }
    })
}
  render() {
    var baseElement = (
        <div className="loader" key="Loader"/>
      );
    if (!this.state.loadingState) {
      baseElement = null;
    }
    return (
      <div>
        <BrowserRouter>
          <div>
            <Navigation />
            <Switch>
              <Route path="/" component={() => <Home unsettled={this.state.unsettled} key="Home-View"/>} exact/>
              <Route component={Error} />
            </Switch>
          </div>
        </BrowserRouter>
        {baseElement}
      </div>
    );
  }
}

export default App;



